<script>
import api from '@/api'
import { getToken } from '@/api/auth'
import TagsAdd from '@/components/TagsAdd'

export default {
  inject: ['reload'],
  data () {
    return {
      formData: {
        name: '',
        type: '',
        original: '2',
        game_methon: '3',
        recom: '1',
        state: '1',
        show_down: '0',
        check_updateTime: '0',
        relation_id: '',
        tags: ''
      },
      ruleValidate: {
        name: [
          { required: true, message: '请输入应用名称', trigger: 'blur' }
        ],
        type: [
            { required: true, message: '请选择应用类型', trigger: 'change' }
        ],
      },
      configData: {},
      groupid: '',
      type_category: '',
      similar_son: [],
      tagInfo: [],
      checkNameStatus: '',
      tagsAddConfig1: {
        url: 'searchAllTagList',
        classify: 2,
        tags: [],
        type:1
      },//关联标签添加配置
      tagsAddConfig2: {
        url: 'searchList',
        classify: 2,
        tags: [],
        type:2,
        tagsMenu: [],
        gameId: this.$route.query.id
      },//关联应用添加配置
      isLoaded: false,
      editAllGameType: 1,//主应用类型 1新增 2编辑
    }
  },
  created () {
    this.editAllGameType = this.$route.query.id ? 2 : 1;
    const token = getToken(),
          id = this.$route.query.id,
          configParams = this.editAllGameType == 1 ? {token} : {token,id};

    this.getConfig(configParams);
  },
  methods: {
    handleSubmitCheck (formName) {
      if(this.formData.name != '') {
        const token = getToken(),
              name = this.formData.name,
              type = 'app',
              id = this.$route.query.id,
              params = {token, name, type, id};

        api.checkAllName(params).then(res =>{
          if(res.code == 0) {
            const tipsArr = ['ok', '名称已存在', '其他编辑正在录入...', '该名称在应用中已存在'];
            res.data[0] == -1 ? this.handleSubmit(formName) : this.$Message.error(tipsArr[res.data[0]]);
          } else {
            this.$Message.error(res.msg)
          }
        })
      } else {
        this.$Message.error('名称不能为空')
      }
    },//表单数据提交前校验
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.$Spin.show();
          let tagListArr = this.clearArrSpace(this.$refs.tagsAddSwitch2.tagListStr.split(','),2),
              tagStr = this.clearArrSpace(this.$refs.tagsAddSwitch1.tagListStr,1);
          const token = getToken(),
                id = this.$route.query.id*1 || '',
                name = this.formData.name,
                type = this.formData.type*1,
                original = this.formData.original*1,
                game_methon = this.formData.game_methon*1,
                recom = this.formData.recom*1,
                state = this.formData.state*1,
                show_down = this.formData.show_down*1,
                source_id = this.formData.source_id*1,
                source_url = this.formData.source_url,
                check_updateTime = this.formData.check_updateTime*1,
                tags = tagStr,
                relation_id = tagListArr,
                classify = 2,
                params = {token,id,name,type,original,game_methon,recom,state,show_down,check_updateTime,tags,relation_id,classify,source_id,source_url};
          // console.log(params)
          api.exeEditAllGame(params).then(res => {
            this.$Spin.hide();
            if(res.code == 0) {
              const timer = setTimeout(() => {
                this.$Modal.remove();
                this.$router.push({"path": '/data/allApp'});
              },5000)
              this.$Modal.confirm({
                title: '操作成功',
                content: '5秒后自动跳转主应用列表页',
                cancelText: '确认',
                okText: '下一篇',
                loading: true,
                onCancel: () => {
                  clearTimeout(timer)
                  this.$router.push({"path": '/data/allApp'});
                },
                onOk: () => {
                  clearTimeout(timer)
                  this.$Modal.remove();
                  this.$router.push({"path": '/data/editAllApp'});
                }
              });
              
            } else {
              this.$Message.error(res.msg)
            }
          })
        }
      })
    },//表单数据提交
    getConfig (configParams) {
      api.editAllApp(configParams).then(res => {
        // console.log(res)
        if(res.code == 0) {
          this.groupid = res.data.groupid
          this.type_category = res.data.type_category
          this.tagInfo = []
          this.similar_son = []
          this.configData = res.data.config
          if(this.editAllGameType == 1) {
            this.formData = {
              name: '',
              type: '',
              original: '2',
              game_methon: '3',
              recom: '1',
              state: '1',
              show_down: '0',
              check_updateTime: '0',
              tags: '',
              relation_id: ''
            }
          } else {
            this.tagInfo = res.data.tagInfo || []
            this.similar_son = res.data.similar_son || []
            const data = res.data.gameInfo
            this.formData = {
              name: data.name || '',
              type: data.type || '',
              original: data.original || '2',
              game_methon: data.game_methon || '3',
              recom: data.recom || '1',
              state: data.state || '1',
              show_down: data.show_down || '0',
              check_updateTime: '0',
              tags: data.tags || '',
              relation_id: res.data.similar_son_str || ''
            }
          }
          this.isLoaded = true
        } else {
          this.$Notice.error({ desc: res.msg })
        }
      })
    },//配置获取
    checkName () {
      if(this.formData.name != '') {
        const token = getToken(),
              name = this.formData.name,
              type = 'app',
              id = this.$route.query.id,
              params = {token, name, type, id};

        api.checkAllName(params).then(res =>{
          if(res.code == 0) {
            const tipsArr = ['ok', '名称已存在', '其他编辑正在录入...', '该名称在应用中已存在'];
            this.checkNameStatus = res.data[0] == -1 ? tipsArr[0] : tipsArr[res.data[0]];
          } else {
            this.$Message.error(res.msg)
          }
        })
      }
    },//应用名称校验
    clearGameNameStatus () {
      this.checkNameStatus = '';
    },//应用、主库应用检测状态清除
    clearArrSpace (data,type) {
      if(type == 2) {
        data.forEach((ele, i) => {
          ele == '' ? data.splice(i,1) : null
        })
        return data;
      } else if (type == 1) {
        return data
      }
      
    },//数组、字符格式化处理
  },
  watch: {
    tagInfo: function() {
      this.tagsAddConfig1.tags = this.tagInfo
      this.tagsAddConfig2.tagsMenu = this.tagInfo
    },
    similar_son: function() {
      this.tagsAddConfig2.tags = this.similar_son
    },
    $route: function(to, from) {
      to.fullPath == from.fullPath ? null : this.reload()
    }
  },
  components: {TagsAdd}
}
</script>
<template>
  <div>
    <h3 v-if="editAllGameType == 1"><Icon type="md-add-circle" size=24 />新增主应用</h3>
    <h3 v-else-if="editAllGameType == 2"><Icon type="md-paper" size=24 />编辑主应用</h3>
    <Form ref="formData" :model="formData" :rules="ruleValidate" :label-width="130">
      <div class="formBox">
        <FormItem label="应用名称" prop="name" class="nameBox">
          <Input 
            v-model="formData.name" 
            clearable 
            @on-blur="checkName"
            @on-clear="clearGameNameStatus()"
            >
          </Input>
          <span :class="{err1: checkNameStatus != 'ok'}">{{checkNameStatus}}</span>
        </FormItem>
        <FormItem label="应用状态" prop="state" v-if="type_category == 1">
          <Select v-model="formData.state">
            <Option value="1">正常</Option>
            <Option value="2">下架</Option>
            <Option value="3">删除</Option>
          </Select>
        </FormItem>
        <FormItem label="是否原创" prop="original">
          <Select v-model="formData.original">
            <Option value="1">是</Option>
            <Option value="2">否</Option>
          </Select>
        </FormItem>
      </div>
      <FormItem label="应用类型" prop="type">
        <RadioGroup v-model="formData.type">
          <Radio v-for="itm, idx in configData.app_type" :label="idx" :key="idx">{{itm}}</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="应用模式" prop="game_methon">
        <RadioGroup v-model="formData.game_methon">
          <Radio v-for="itm, idx in configData.game_methon" :label="idx" :key="idx">{{itm}}</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="推荐设置" prop="recom" class="smallest">
        <Select v-model="formData.recom">
          <Option v-for="itm, idx in configData.game_recom" :value="idx" :key="idx">{{itm}}</Option>
        </Select>
      </FormItem>
      <FormItem label="标签关联" prop="tags">
        <TagsAdd 
          :tagsAddConfig="tagsAddConfig1"
          ref="tagsAddSwitch1"
          v-if="isLoaded"
        />
      </FormItem>
      <FormItem label="关联应用" prop="tags">
        <TagsAdd 
          :tagsAddConfig="tagsAddConfig2"
          ref="tagsAddSwitch2"
          v-if="isLoaded"
        />
      </FormItem>
      <FormItem label="是否更改更新时间" prop="check_updateTime">
        <RadioGroup v-model="formData.check_updateTime">
          <Radio label='1'>是</Radio>
          <Radio label='0'>否</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="展示下载聚合页" prop="show_down">
        <RadioGroup v-model="formData.show_down">
          <Radio label='1'>是</Radio>
          <Radio label='0'>否</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmitCheck('formData')">确认提交</Button>
      </FormItem>
    </Form>
  </div>
</template>
<style lang="scss" scoped>
  .smallest {
    width: 13%;
  }
  .small {
    width: 25%;
  }
  .default {
    width: 40%;
  }
  .big {
    width: 70%;
  }
  .formBox {
    display: flex;
  }
  .nameBox {
      margin-right: 82px;
      span {
        width: 120px;
        position: absolute;
        color: green;
        margin-left: 6px;
        &.err1 {
          color: red;
        }
      }
    }
</style>