<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default {
  name: 'tagsAdd',
  props: {
    /**
     * 配置参数获取
     * url 标签列表获取地址
    */
    tagsAddConfig: { type: Object}
  },
  data () {
    return {
      openTagAdd: false,
      selval: '',
      tagList: [],
      tagListStr: '',
      relevanceTagList: {},
      tagsMenu: [],
      tagsMenuAdd: [],
      getRelevanceTagParams1: {
        token: getToken(),
        page: 1,
        per_page: 40,
        classify: 1,
        selval: '',
      },
      getRelevanceTagParams2: {
        token: getToken(),
        gameid: this.tagsAddConfig.gameId,
        page: 1,
        per_page: 40,
        classify: 1,
        selval: '',
        tags_id: '',
        tagsid1: '0',
        tagsid2: '0',
        tagsid3: '0',
      },
    }
  },
  created () {
    this.tagList = this.tagsAddConfig.tags || []
    this.tagsMenu = this.tagsAddConfig.tagsMenu || []
    this.getTagListStr(this.tagList)
    this.getRelevanceTag();
  },
  methods: {
    openTagsAddFun () {
      // console.log(this.tagsMenuAdd)
      this.openTagAdd = true
    },
    getRelevanceTag (flag) {
      this.getRelevanceTagParams1.selval = this.selval;
      this.getRelevanceTagParams2.selval = this.selval;
      this.getRelevanceTagParams1.classify = this.tagsAddConfig.classify || 1;
      this.getRelevanceTagParams2.classify = this.tagsAddConfig.classify || 1;
      if(flag) {this.tagsAddConfig.type == 1 ? this.getRelevanceTagParams1.page = 1 : this.getRelevanceTagParams2.page = 1}
      let params = this.tagsAddConfig.type == 1 ?  this.getRelevanceTagParams1 : this.getRelevanceTagParams2;
      api[this.tagsAddConfig.url](params).then(res => {
        if(res.code == 0) {
          this.relevanceTagList = res.data
        } else {
          this.$Notice.error({
            desc: res.msg
          })
        }
      })
    },//标签列表执行函数
    addRelevanceTag (item) {
      let isHave = this.tagListStr.split(',').some(ele => {
        return ele == item.id
      })//关联标签查重
      console.log(this.tagListStr,item.id, isHave)
      if(!isHave) {
        this.tagListStr = this.tagListStr+=(item.id+',')
        this.tagList.push({id:item.id, name: item.name})
        this.$Message.success('关联添加成功')
        this.tagsAddConfig.type == 1 ? this.tagsMenuAdd = this.tagList : null;
        // console.log(this.tagListStr, this.tagList,this.tagsMenuAdd)
      } else {
        this.$Message.warning('当前关联标签id已经添加，请不要重复添加')
      }
    },//标签添加
    delRelevanceTag (id, idx) {
      let reg = new RegExp(id+',?')
      this.tagListStr = this.tagListStr.replace(reg, '');
      this.tagList.splice(idx, 1)
      // console.log(id, idx)
      // console.log(this.tagListStr, this.tagList)
    },//标签删除
    changePage (page) {
      this.tagsAddConfig.type == 1 ? this.getRelevanceTagParams1.page = page*1 : this.getRelevanceTagParams2.page = page*1;
      this.getRelevanceTag();
    },//标签列表分页
    changePageNum (num) {
      this.tagsAddConfig.type == 1 ? this.getRelevanceTagParams1.page = 1 : this.getRelevanceTagParams2.page = 1;
      this.tagsAddConfig.type == 1 ? this.getRelevanceTagParams1.per_page = num*1 : this.getRelevanceTagParams2.per_page = num*1;
      this.getRelevanceTag();
    },//标签列表每页显示数量
    getTagListStr (tagsList) {
      let tagsStr = ''
      if(tagsList && tagsList.length>0) {
        tagsList.forEach(ele => {
          tagsStr+=((ele.id || ele.sid)+',')
        })
      } else {
        tagsStr = ''
      }
      
      this.tagListStr = tagsStr
    }//标签列表id字符串集
  }
}
</script>
<template>
  <div>
    <!-- <input type="hidden" v-model="formData.tags"> -->
    <Tag v-for="item,idx in tagList" :key="idx" :name="item.name" closable @on-close="delRelevanceTag(item.id || item.sid, idx)">{{item.name+(item.id||item.sid)}}</Tag>
    <Button icon="ios-add" type="dashed" size="small" @click="openTagsAddFun">添加标签</Button>
    <Modal v-model="openTagAdd" width="800">
      <p slot="header">
        <Icon type="ios-add-circle" size=18 />
        <span style="margin-left:4px">关联添加</span>
      </p>
      <div class="tagBox">
        <div style="display:flex">
          <Input v-model="selval" clearable search enter-button placeholder="在此输入名称搜索" class="small" @on-search="getRelevanceTag(true)" />
          <Select v-model="getRelevanceTagParams2.tagsid1" v-if="tagsAddConfig.type == 2" style="width:100px;margin: 1px 0 0 10px" @on-change="getRelevanceTag(true)">
            <Option value="0">标签</Option>
            <Option v-for="itm,idx in tagsMenu" :value="itm.id" :key="idx">{{itm.name}}</Option>
          </Select>
          <Select v-model="getRelevanceTagParams2.tagsid2" v-if="tagsAddConfig.type == 2" style="width:100px;margin: 1px 0 0 10px" @on-change="getRelevanceTag(true)">
            <Option value="0">标签</Option>
            <Option v-for="itm,idx in tagsMenu" :value="itm.id" :key="idx">{{itm.name}}</Option>
          </Select>
          <Select v-model="getRelevanceTagParams2.tagsid3" v-if="tagsAddConfig.type == 2" style="width:100px;margin: 1px 0 0 10px" @on-change="getRelevanceTag(true)">
            <Option value="0">标签</Option>
            <Option v-for="itm,idx in tagsMenu" :value="itm.id" :key="idx">{{itm.name}}</Option>
          </Select>
        </div>
        <div class="tagList" v-if="relevanceTagList.gamelist && relevanceTagList.gamelist.length>0">
          <span 
            v-for="itm,idx in relevanceTagList.gamelist" 
            @click="addRelevanceTag(itm)"
            :key="idx">
            {{itm.name}}
          </span>
        </div>
        <div class="tagListEmpty" v-else>
          <div><Icon type="ios-ionitron-outline" size=26 />查询为空</div>
        </div>
      </div>
      <div slot="footer">
        <Page :total="relevanceTagList.totlerows*1" :page-size=40 size="small" show-total show-sizer style="text-align: right;" @on-page-size-change="changePageNum" @on-change="changePage"  />
      </div>
    </Modal>
  </div>
</template>
<style lang="scss" scoped>
  .smallest {
    width: 13%;
  }
  .small {
    width: 25%;
  }
  .default {
    width: 40%;
  }
  .big {
    width: 70%;
  }
  .tagList {
    margin: 10px 0 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    span {
      display: inline-block;
      text-align: center;
      padding: 15px;
      cursor: pointer;
      &:hover {
        color: #2db7f5;
      }
    }
  }
  .tagListEmpty {
    margin: 10px 0 0;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 200px;
    line-height: 200px;
    text-align: center;
    font-size: 17px;
    i {
      position: relative;
      top: -4px
    }
  }
</style>